<template>
  <div>
    <div>
      <el-card shadow='hover' class='card'>
        <el-divider class='divider' content-position="right" @click='closeHollowDetail'>
          <div class='back'>
            <el-image class='backIcon' :src="require('@/../public/assets/return.png')"></el-image>
            <span class='backWord'>{{$t('Hollow.DetailReturn')}}</span>
          </div>
        </el-divider>

        <div class='content'>
          <div class='user'>
            <span class='avatar'>
              <el-avatar shape="square" :size="80" :src="avatar"></el-avatar>
            </span>
            <span class='userInfo'>
              <div class='username' @click='openUserPage(userid, nick)'>{{username}}</div>
              <div class='school'>{{school}}</div>
            </span>
          </div>

          <div class='middle'>
            <div class='detail'>{{detail}}</div>
          </div>

          <div class='picture' v-if='picture.length'>
            <span v-for='item in picture' v-bind:key='item'>
              <span class='pictureItem'>
                <el-image :src="item" width="200px"></el-image>
              </span>
            </span>
          </div>
        </div>

        <div class='footer'>
          <div class='action'>
            <span class='time'>{{time}}</span>
            <span class='buttons'>
              <span v-if='!alreadyLike' @click='clickLike' class='like'>
                <el-image class='aAction' :src="require('@/../public/assets/like.png')"></el-image>
                <span>({{like}})</span>
              </span>
              <span v-if='alreadyLike' @click='clickLike' class='like'>
                <el-image class='aAction' :src="require('@/../public/assets/liked.png')"></el-image>
                <span>({{like}})</span>
              </span>
              <el-image class='aAction' :src="require('@/../public/assets/share.png')" @click='clickShare'>
              </el-image>
              <el-image class='aAction' :src="require('@/../public/assets/delete.png')" v-if='isOwner||isAdmin'
                @click='clickDelete'>
              </el-image>
              <el-image class='aAction' :src="require('@/../public/assets/report.png')" v-else
                @click='clickReport("hd",postID)'>
              </el-image>
            </span>
          </div>

          <div class='comment'>
            <span class='commentBox'>
              <el-input class='inputBox' :placeholder="this.$t('Hollow.DetailCommentTip')" type="textarea" resize='none'
                v-model='inputComment' rows='3'></el-input>
            </span>
            <span class='commentButtons'>
              <el-badge :value='commentsAmount'>
                <el-button type='primary' @click='submitComment(0)'>{{$t('Hollow.DetailCommentSend')}}</el-button>
              </el-badge><br>
              <el-checkbox v-model="nickComment">{{$t('Hollow.DetailCommentSendNick')}}</el-checkbox>
            </span>
          </div>
        </div>
      </el-card>

      <div class='comments'>
        <div v-for='item in comments' v-bind:key="item.commentid" class='aComment' :ref='item.commentid'>
          <el-card shadow='hover' class='card'>
            <div class='commentTop'>
              <span class='commentUsername' @click='openUserPage(item.userid, item.nick)'>{{item.username}}</span>
              <span class='commentSchool'>{{item.school}}</span>
              <span class='commentTime'>{{item.time}}</span>
            </div>
            <div class='commentContent'>
              <span class='commentDetail'>{{item.content}}</span>
            </div>
            <div class='commentRight'>
              <el-image class='aAction' :src="require('@/../public/assets/delete.png')" v-if='item.isOwner||isAdmin'
                @click='deleteComment(item.commentid)'>
              </el-image>
              <el-image class='aAction' :src="require('@/../public/assets/report.png')" v-else
                @click='clickReport("hc", item.commentid)'>
              </el-image>
            </div>
          </el-card>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import Cookies from 'js-cookie'
import * as server from '../../../server.js'
import { customAlphabet } from 'nanoid';
const nanoid = customAlphabet('1234567890', 9);

export default {
  name: 'hollow-detail',
  data () {
    return {
      avatar: '',
      postID: 0,
      nick: 0,
      userid: 0,
      username: 'USERNAME',
      school: 0,
      detail: 'CONTENT',
      time: '',
      like: 0,
      picture: [],
      comments: [],
      commentsAmount: 0,
      inputComment: '',
      nickComment: false,
      alreadyLike: false,
      isOwner: false,
      isAdmin: false
    }
  },
  methods: {
    clickShare () {
      this.copyLink(this.$t('Hollow.DetailShared'));
    },
    closeHollowDetail () {
      this.$bus.$emit('showHollowList')
    },
    getHollowDetail () {
      const loadingInstance = this.loading();
      axios({
        method: 'post',
        url: server.default.getHollowItem,
        data: new URLSearchParams({
          'postid': this.postID,
          'cookie': Cookies.get('loginCookie')
        })
      }).then((response) => {
        loadingInstance.close();
        if (response.status != 200) {
          this.alert(this.$t('System.NetworkIssue'), this.$t('System.Error'))
        }
        else {
          if (response.data.status == 'HOLLOW_GETITEM_COMPLETE') {
            if (this.postID == response.data.postid) {
              this.nick = response.data.nick;
              this.detail = response.data.content;
              this.like = response.data.like;
              this.userid = response.data.userid;
              this.isOwner = response.data.isOwner;
              this.username = this.formatUsername(response.data.username, this.nick, this.isOwner);
              this.school = this.formatSchool(response.data.school);
              this.time = this.formatTime(response.data.time);
              if (response.data.picture.length > 0) {
                for (let i in response.data.picture.split(',')) {
                  this.picture.push(server.default.server + response.data.picture.split(',')[i])
                }
              }
              this.comments = response.data.comments;
              this.commentsAmount = this.comments.length;
              for (let i in this.comments) {
                this.comments[i].username = this.formatUsername(this.comments[i].username, this.comments[i].nick, this.comments[i].isOwner);
                this.comments[i].time = this.formatTime(this.comments[i].time);
                this.comments[i].school = this.formatSchool(this.comments[i].school);
              }
              this.alreadyLike = response.data.alreadyLike;
              this.avatar = this.getUserAvatar(this.userid);

              setTimeout(() => {
                if (localStorage.getItem('adminworkReportShowOriginalCommentID')) {
                  let target = localStorage.getItem('adminworkReportShowOriginalCommentID');
                  if (this.$refs[target][0]) {
                    this.$refs[target][0].scrollIntoView({ behavior: "smooth", block: "center" });
                    this.$refs[target][0].style = 'background-color: red;border-radius:15px;'
                  }
                  localStorage.removeItem('adminworkReportShowOriginalCommentID')
                }
              }, 1000)
            }
          }
          else {
            this.notfound();
          }
        }
      })
    },
    clickLike () {
      if (this.alreadyLike) {
        this.$message.error(this.$t('Hollow.DetailLiked'));
      }
      else {
        const loadingInstance = this.loading();

        axios({
          method: 'post',
          url: server.default.postHollowLike,
          data: new URLSearchParams({
            'cookie': Cookies.get('loginCookie'),
            'postid': this.postID,
          })
        }).then((response) => {
          loadingInstance.close();
          if (response.status != 200) {
            this.alert(this.$t('System.NetworkIssue'), this.$t('System.Error'))
          }
          else {
            if (response.data == 'HOLLOW_LIKE_COMPLETE') {
              this.redirect('hollowDetail', this.postID);
            }
          }
        })
      }
    },
    clickDelete () {
      if (this.isOwner || this.isAdmin) {
        this.$confirm(this.$t('Hollow.DetailDeleteWarn'), this.$t('System.Warning'), {
          confirmButtonText: this.$t('System.Yes'),
          cancelButtonText: this.$t('System.No'),
          type: 'warning'
        }).then(() => {
          const loadingInstance = this.loading();

          axios({
            method: 'post',
            url: server.default.deleteHollowItem,
            data: new URLSearchParams({
              'cookie': Cookies.get('loginCookie'),
              'postid': this.postID,
            })
          }).then((response) => {
            loadingInstance.close();
            if (response.status != 200) {
              this.alert(this.$t('System.NetworkIssue'), this.$t('System.Error'))
            }
            else if (response.data == 'HOLLOW_DELETE_COMPLETE') {
              this.$message.success(this.$t('Hollow.DetailDeleteComplete'));
              this.$bus.$emit('refreshHollowList');
              this.$bus.$emit('showHollowList');
            }
          })
        }).catch(() => { });
      }
    },
    deleteComment (commentid) {
      for (let i of this.comments) {
        if (commentid == i.commentid || this.isAdmin) {
          this.$confirm(this.$t('Hollow.DetailDeleteWarn'), this.$t('System.Warning'), {
            confirmButtonText: this.$t('System.Yes'),
            cancelButtonText: this.$t('System.No'),
            type: 'warning'
          }).then(() => {
            const loadingInstance = this.loading();

            axios({
              method: 'post',
              url: server.default.deleteHollowComment,
              data: new URLSearchParams({
                'cookie': Cookies.get('loginCookie'),
                'postid': this.postID,
                'commentid': commentid
              })
            }).then((response) => {
              loadingInstance.close();
              if (response.status != 200) {
                this.alert(this.$t('System.NetworkIssue'), this.$t('System.Error'))
              }
              else if (response.data == 'HOLLOW_DELETE_COMPLETE') {
                this.$message.success(this.$t('Hollow.DetailDeleteComplete'));
                this.getHollowDetail();
              }
            })
          }).catch(() => { });
          break;
        }
      }
    },
    clickReport (target, targetid) {
      this.$prompt(this.$t('General.ReportReason'), this.$t('General.Report'), {}).then((value) => {
        if (!value.value) {
          return;
        }
        else {
          this.report(Cookies.get('loginCookie'), target, targetid, value.value, location.href);
        }
      })
    },
    submitComment () {
      if (this.inputComment.length < 2 || this.inputComment.length > 40) {
        this.alert(this.$t('Hollow.DetailCommentBadLength'), this.$t('System.Warning'));
      }
      else {
        const loadingInstance = this.loading();

        axios({
          method: 'post',
          url: server.default.postHollowComment,
          data: new URLSearchParams({
            'cookie': Cookies.get('loginCookie'),
            'content': this.inputComment,
            'postid': this.postID,
            'commentid': nanoid(),
            'isNick': this.nickComment ? 1 : 0,
            'alertid': nanoid(),
            'link': location.href
          })
        }).then((response) => {
          loadingInstance.close();
          if (response.status != 200) {
            this.alert(this.$t('System.NetworkIssue'), this.$t('System.Error'))
          }
          else {
            if (response.data == 'HOLLOW_COMMENTID_TAKEN' || response.data == 'HOLLOW_COMMENT_ALERTIDTAKEN') {
              this.submitComment();
              return;
            }
            else if (response.data == 'HOLLOW_COMMENT_COMPLETE') {
              this.redirect('hollowDetail', this.postID);
            }
          }
        })
      }
    },
  },
  mounted () {
    this.postID = this.$route.params.id;
    this.getHollowDetail();
    this.isAdmin = this.getIfAdmin();
  }
}
</script>

<style scoped>
.card {
  margin-left: 5%;
  width: 90%;
  border-radius: 20px;
}
.divider {
  width: 90%;
  margin-left: 5%;
}
.back {
  cursor: pointer;
}
.backIcon {
  color: rgb(252, 62, 5);
  width: 20px;
}
.backWord {
  font-size: 18px;
  margin-left: 10px;
}
.content {
  margin-top: 25px;
  text-align: left;
  margin-left: 10%;
  margin-bottom: 40px;
  width: 80%;
}
.user {
  display: flex;
  margin-bottom: 30px;
}
.user .userInfo {
  margin-left: 20px;
  font-size: 20px;
}
.user .userInfo .username {
  font-size: 30px;
  margin-bottom: 10px;
}
.username {
  font-size: 30px;
  margin-right: 25px;
  font-weight: bold;
  cursor: pointer;
}
.school {
  margin-right: 25px;
}
.middle {
  font-size: 20px;
}
.picture {
  margin-top: 20px;
  margin-bottom: 30px;
}
.picture .pictureItem {
  margin-right: 15px;
}
.footer {
  margin-bottom: 30px;
}
.action {
  width: 80%;
  margin-left: 10%;
  display: flex;
  justify-content: space-between;
}
.like span {
  position: relative;
  top: -6px;
  left: -15px;
}
.time {
  text-align: left;
}
.buttons {
  text-align: right;
  font-size: 20px;
  margin-bottom: 20px;
}
.aAction {
  width: 25px;
  margin-right: 15px;
  cursor: pointer;
}
.inputBox {
  width: 55%;
  margin-right: 25px;
  font-size: 18px;
  border-radius: 10px;
}
.inputBox ::v-deep textarea {
  border-radius: 15px;
}
.commentButtons .el-button {
  font-size: 15px;
  border-radius: 15px;
}
.commentButtons {
  display: inline-block;
  position: relative;
  top: -10px;
}
.comment {
  margin-bottom: 10px;
}
.aComment {
  text-align: left;
  width: 90%;
  margin-left: 5%;
  margin-top: 30px;
}
.commentUsername {
  font-size: 25px;
  font-weight: bold;
  cursor: pointer;
}
.commentSchool {
  margin-left: 20px;
  margin-right: 20px;
}
.commentContent {
  margin-top: 15px;
  width: 100%;
}
.commentDetail {
  width: 80%;
}
.commentRight {
  float: right;
  margin-right: 5%;
  margin-bottom: 15px;
}
</style>